<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";
export default {
  props: {
    title: {},
    values: { default: [] },
    monthSelect: { default: true },
  },
  mounted() {
    this.$unlock({ monthSelect: this.monthSelect });
  },
  computed: {
    option() {
      let values = this.values;

      return {
        title: {
          text: this.title,
          left: "center",
          top: 12,
          textStyle: {
            color: "#03D3D3",
            fontSize: 16,
          },
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)",
        },
        legend: {
          left: "center",
          bottom: 10,
          itemWidth: 10,
          itemHeight: 8,
          textStyle: {
            color: "#03D3D3",
            fontSize: 12,
          },
          data: values.map((name) => name),
        },
        series: [
          {
            name: "购物场所",
            type: "pie",
            radius: [10, 50],
            center: ["50%", "50%"],
            roseType: "area",
            color: ["#8378EA", "#E7BCF3", "#FF9F7F", "#FFDB5C", "#9FE6B8"],
            data: values.sort(function (a, b) {
              return a.value - b.value;
            }),
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function () {
              return Math.random() * 200;
            },
          },
        ],
      };
    },
  },

  components: {
    Echart,
  },
};
</script>

<style scoped></style>
