<template>
  <div>
    <a-title :title="'热门旅游线路日均搜索量'" />
    <card class="h240" unlock>
      <top-bar :monthSelect="false" :left="left" :right="right" :max="max" :values="values" />
    </card>
  </div>
</template>
<script>
import Card from "@/components/Card.vue";
import TopBar from "@/components/charts/TopBar.vue";

import ATitle from "./@ATitle.vue";

export default {
  mounted() {
    this.$getData("/analysis/tourist-route-left").then((list) => {
      this.list = list;
      this.$unlock();
    });
  },
  data() {
    return {
      list: [],
    };
  },
  computed: {
    left() {
      return this.list.map(({ type }) => type);
    },
    values() {
      return this.list.map(({ value }) => Number(value));
    },
    right() {
      return this.values.map((item) => item + "元");
    },
    max() {
      return Math.max(...this.values) * 1.2;
    },
  },
  components: {
    Card,
    TopBar,
    ATitle,
  },
};
</script>
<style scoped>
.h240 {
  height: 240px;
}
</style>