<template>
  <div class="analysis">
    <card style="width:100%;height:100%;background:#000;">
      <gd-map />
    </card>
    <div class="nav">
      <span
        v-for="(nav,index) in navs"
        :key="index"
        class="nav-item"
        :class="{ active: nav.path === active }"
        @click="switchTo(nav.path)"
      >{{ nav.name }}</span>
    </div>

    <div class="left">
      <component v-bind:is="leftComponent"></component>
    </div>
    <div class="right">
      <component v-bind:is="rightComponent"></component>
    </div>
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import GdMap from "@/components/GdMap.vue";

import CulturalTourismLeft from "@/charts/analysis/CulturalTourismLeft.vue";
import CulturalTourismRight from "@/charts/analysis/CulturalTourismRight.vue";
import ScenicAreaLeft from "@/charts/analysis/ScenicAreaLeft.vue";
import ScenicAreaRight from "@/charts/analysis/ScenicAreaRight.vue";
import HotelAccomodationLeft from "@/charts/analysis/HotelAccomodationLeft.vue";
import HotelAccomodationRight from "@/charts/analysis/HotelAccomodationRight.vue";
import DiningEntertainLeft from "@/charts/analysis/DiningEntertainLeft.vue";
import DiningEntertainRight from "@/charts/analysis/DiningEntertainRight.vue";
import TravelShoppingLeft from "@/charts/analysis/TravelShoppingLeft.vue";
import TravelShoppingRight from "@/charts/analysis/TravelShoppingRight.vue";
import TouristRouteLeft from "@/charts/analysis/TouristRouteLeft.vue";
import TouristRouteRight from "@/charts/analysis/TouristRouteRight.vue";
import TourismEvaluationLeft from "@/charts/analysis/TourismEvaluationLeft.vue";
import TourismEvaluationRight from "@/charts/analysis/TourismEvaluationRight.vue";
import TouristToiletLeft from "@/charts/analysis/TouristToiletLeft.vue";
import TouristToiletRight from "@/charts/analysis/TouristToiletRight.vue";

const NAVS = [
  {
    path: "/culturalTourism",
    name: "文旅产业数据",
    left: CulturalTourismLeft,
    right: CulturalTourismRight,
  },
  {
    path: "/scenicArea",
    name: "景区数据",
    left: ScenicAreaLeft,
    right: ScenicAreaRight,
  },
  {
    path: "/hotelAccomodation",
    name: "酒店住宿",
    left: HotelAccomodationLeft,
    right: HotelAccomodationRight,
  },
  {
    path: "/diningEntertain",
    name: "餐饮娱乐",
    left: DiningEntertainLeft,
    right: DiningEntertainRight,
  },
  {
    path: "/travelShopping",
    name: "旅游购物",
    left: TravelShoppingLeft,
    right: TravelShoppingRight,
  },
  {
    path: "/touristRoute",
    name: "旅游路线",
    left: TouristRouteLeft,
    right: TouristRouteRight,
  },
  {
    path: "/tourismEvaluation",
    name: "旅游服务评价",
    left: TourismEvaluationLeft,
    right: TourismEvaluationRight,
  },
  {
    path: "/touristToilet",
    name: "旅游厕所",
    left: TouristToiletLeft,
    right: TouristToiletRight,
  },
];

export default {
  name: "analysis",
  data() {
    return {
      active: NAVS[0].path,
      navs: NAVS,
    };
  },
  computed: {
    activeNav() {
      return this.navs.find((item) => item.path === this.active);
    },
    leftComponent() {
      return this.activeNav && this.activeNav.left;
    },

    rightComponent() {
      return this.activeNav && this.activeNav.right;
    },
  },
  methods: {
    switchTo(path) {
      this.active = path;
    },
  },
  components: {
    Card,
    GdMap,
  },
};
</script>

<style lang="less" scoped>
.analysis {
  position: relative;
  height: 100%;
  background: #000;
  .amap-maps{
    background: #000;
  }
  .nav {
    position: absolute;
    bottom: 34px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;

    .nav-item {
      display: flex;
      align-items: center;
      padding: 0 12px;
      margin: 0 6px;
      height: 32px;
      background: rgba(0, 255, 255, 0.15);
      font-weight: bold;
      color: rgba(0, 255, 255, 1);
      border: 1px solid rgba(0, 255, 255, 0);
      cursor: pointer;

      &.active {
        border: 1px solid rgba(0, 255, 255, 1);
      }
    }
  }

  .left,
  .right {
    position: absolute;
    top: 140px;
    width: 320px;
    height: 400px;
    overflow: hidden;
  }

  .left {
    left: 24px;
  }

  .right {
    right: 24px;
  }
}
</style>
