<template>
  <div>
    <a-title :title="'旅游路线分类占比'" />
    <card class="h240" unlock>
      <rose :monthSelect="false" :values="values" />
    </card>
  </div>
</template>
<script>
import Card from "@/components/Card.vue";
import Rose from "@/components/charts/Rose.vue";

import ATitle from "./@ATitle.vue";

export default {
  mounted() {
    this.$getData("/analysis/tourist-route-right").then((list) => {
      this.list = list;
    });
  },
  data() {
    return {
      list: [],
    };
  },
  computed: {
    values() {
      return this.list.map(({ type, value }) => ({ name: type, value }));
    },
  },
  components: {
    Card,
    Rose,
    ATitle,
  },
};
</script>
<style scoped>
.h240 {
  height: 240px;
}
</style>